import React from 'react';
import { node, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-pagination';

const Pagination = ({ ariaLabel, children, className, role, ...remainingProps }) => (
  <nav
    role={role}
    aria-label={ariaLabel}
    {...remainingProps}
  >
    <ul className={classnames(CSS_PREFIX, className)}>
      {children}
    </ul>
  </nav>
);

Pagination.propTypes = {
  /**
   * Aria label
   */
  ariaLabel: string.isRequired,

  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string,

  /**
   * @ignore
   */
  role: string
};

Pagination.defaultProps = {
  role: 'navigation'
};

Pagination.displayName = 'Pagination';

export default Pagination;
